import axios from 'axios'
let FormData = require('form-data');


export function createArticle(title, description, token, imageUrl, price, aisle, quantity) {

    let article = {
        title: title,
        description: description,
        price: price,
        aisle: aisle,
        quantity: quantity
    }
    let form = new FormData();
    form.append('image', imageUrl)
    form.append('article', JSON.stringify(article))



    return axios.post('https://hospiback.herokuapp.com/api/articles/', form, {
        headers: {
            'Authorization': `bearer ${token}`
        }
    })
}


export function getAllArticles(token, page, aisle) {
    return axios.get('https://hospiback.herokuapp.com/api/articles', {
        headers: {
            'Authorization': `bearer ${token}`

        },
        params: {
            page: page,
            aisle: aisle
        }
    })
}


export function getArticleById(articleId, token) {
    return axios.get(`https://hospiback.herokuapp.com/api/articles/${articleId}`, {
        headers: {
            'Authorization': `bearer ${token}`

        }
    })

}

export function updateArticleById(articleId, title, description, token, imageUrl, price, aisle, quantity) {
    return axios.put(`https://hospiback.herokuapp.com/api/articles/${articleId}`, {
        title: title,
        description: description,
        imageUrl: imageUrl,
        price: price,
        aisle: aisle,
        quantity: quantity

    }, {
        headers: {
            'Authorization': `bearer ${token}`
        }
    })
}

export function deleteArticleById(articleId, token) {
    return axios.delete(`https://hospiback.herokuapp.com/api/articles/${articleId}`, {
        headers: {
            'Authorization': `bearer ${token}`
        }
    })
}