<template>
  <section class="home">
      <header v-if="show" class="header">
        <nav class="header__nav">
          <ul class="header__nav__ul">
            <li @click="maternity" class="header__nav__ul__li">Marternité</li>
            <li @click="flower" class="header__nav__ul__li">Fleuriste</li>
            <li @click="usefull" class="header__nav__ul__li">Les essentiels</li>
            <li @click="snack" class="header__nav__ul__li">Snack</li>
            <li @click="gifts" class="header__nav__ul__li">Cadeaux</li>
          </ul>
        </nav>

      </header>

      <div v-if="!show" @click="showHeader" class="show_header_btn"><i class="fas fa-list"></i></div>
      <div v-if="show" @click="hideHeader" class="show_header_btn"><i class="fas fa-arrow-left"></i></div>
      

      <div v-if="detail"  class="article_modal">
        <div class="article_modal__box">
                  <p @click="closeModal" class="article_modal__close_button">Fermer</p>
          <div class="article_modal__row">
            <div class="article_modal__box__text">
             <h3>{{ oneArticle.title }}</h3>
            <p>{{ oneArticle.price }}€</p>
            <p>{{ oneArticle.description }}</p>
            <button @click="addToBasket(`${oneArticle.title}`,`${oneArticle._id}`,`${oneArticle.price}`)">Ajouter au panier</button>

            </div> 
           
            <img :src="oneArticle.imageUrl" :alt="oneArticle.title" class="article_modal__box__img">

          </div>
        </div>
      </div>

     <div class="box_result">
        <div @click="showarticle(`${article._id}`)" v-for="article in articlesArray" :key="article.id" class="results" :id="article._id">
          <div class="results__text">
              <h3 class="results__text__title">{{ article.title }}</h3>
              <p class="results__text__price"> {{ article.price }}€</p>
          </div>
          <img :src="article.imageUrl" :alt="article.title" class="results__img">
        </div>
     </div>
     
  </section>
</template>

<script>
// @ is an alias to /src
import { mapState } from 'vuex'
import {getAllArticles, getArticleById} from '../api/articles.api'



export default {
  name: 'Home',
   computed: {
    ...mapState(['token'])
    },
  data(){
    return{
    articlesArray:[],
    page:0,
    aisle:"Maternité",
    oneArticle: [],
    detail: false,
    show:true,
    
    }
  },
 

  methods:{
    apiget(){
      getAllArticles(this.token, this.page, this.aisle)
        .then(res=>{
          res.data.forEach(data => {
            this.articlesArray.push(data) 
            console.log(this.articlesArray)
          });
        })
        .catch(err=>{
                  console.log(err)
              })
    },
    
    resizeWindow(){
         let newWidth = window.innerWidth; 
         if( newWidth <= 900){
         this.show = false
       }else if(newWidth > 900){
         this.show = true
       }
       console.log(newWidth)
       },
    showHeader(e){
      e.preventDefault()
      this.show=true
    
    },
    hideHeader(){
      this.show=false
    },
    closeModal(){
      
      this.detail=false
    },
    maternity(){
      this.aisle = "Maternité"
      this.articlesArray = []
      this.apiget()
      this.resizeWindow()
      
    },
     flower(){
      this.aisle = "Fleuriste"
      this.articlesArray = []
      this.apiget()
      this.resizeWindow()

    },  
    usefull(){
      this.aisle = "Les essentiels"
      this.articlesArray = []
      this.apiget()
      this.resizeWindow()

    },  
    snack(){
      this.aisle = "Snack"
      this.articlesArray = []
      console.log(this.aisle)
      this.apiget()
      this.resizeWindow()

    },
    gifts(){
      this.aisle = "Cadeaux"
      this.articlesArray = []
      this.apiget()
      this.resizeWindow()

    },
    showarticle(id){
      console.log(id)
      getArticleById(id, this.token)
      .then(res=>{
        console.log(res)
            this.oneArticle = res.data 
            console.log(res.data)
            this.detail=true
      })
      .catch(err=>{
        console.log(err)
      })


    },
    addToBasket(name,id,price){
      if (localStorage.getItem("basket") == null) {
            localStorage.setItem("basket", '[]');
        }
      

      let ProductsInfosForLocalStorage = {
              title : name,
              price : price,
              id : id,
      };

      let basket = JSON.parse(localStorage.getItem("basket"));

      basket.push(ProductsInfosForLocalStorage);

      localStorage.setItem("basket", JSON.stringify(basket));

      this.detail=false
    }
  },
  mounted(){
      this.apiget()
       
       
      window.addEventListener('resize',this.resizeWindow())
       
  
  }
}
</script>
